import React, { useState, useEffect, useMemo } from 'react';
import { supabase } from './supabaseClient';
import { Line } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, Filler } from 'chart.js';
import { format, sub } from 'date-fns';
import { Brightness4Icon, Brightness7Icon, ArrowUpwardIcon, ArrowDownwardIcon } from './LocalIcons';
import SerialMonitor from './SerialMonitor';

// MUI Imports
import {
  Container, Grid, Card, CardContent, Typography, CircularProgress, Box, Alert, AlertTitle, Paper, AppBar, Toolbar, IconButton, ButtonGroup, Button, Tooltip as MuiTooltip
} from '@mui/material';

// --- Rejestracja Chart.js ---
ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, Filler);

// --- Komponenty Pomocnicze ---

const StatCard = ({ title, value, unit, icon, trend, tooltip }) => (
  <MuiTooltip title={tooltip || ''} placement="top" arrow>
    <Card sx={{ height: '100%', minHeight: '140px' }}> {/* Zwiększono minHeight */}
      <CardContent>
        <Typography color="text.secondary" variant="subtitle2" gutterBottom sx={{ mb: 1 }}>{title}</Typography>
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Typography variant="h3" component="div" sx={{ mr: 1.5, color: 'primary.main' }}>{icon}</Typography>
            <Typography variant="h4" component="div">{value ?? '--'}<span style={{ fontSize: '1.2rem' }}>{unit}</span></Typography>
          </Box>
          {trend && (
            <Box sx={{ display: 'flex', alignItems: 'center', color: trend > 0 ? 'success.main' : 'error.main' }}>
              {trend > 0 ? <ArrowUpwardIcon/> : <ArrowDownwardIcon/>}
              <Typography variant="subtitle2">{trend.toFixed(2)}</Typography>
            </Box>
          )}
        </Box>
      </CardContent>
    </Card>
  </MuiTooltip>
);

const MainWeatherDisplay = ({ data }) => {
    if (!data) return null;
    const { temperature_c, is_raining, cloud_cover_pct, is_daylight, is_foggy } = data;

    const getWeatherIcon = () => {
        if (is_foggy) return '🌫️';
        if (is_raining) return '🌧️';
        if (cloud_cover_pct > 60) return '☁️';
        if (cloud_cover_pct > 20) return is_daylight ? '⛅' : '☁️';
        return is_daylight ? '☀️' : '🌙';
    }

    return (
        <Paper sx={{ p: 3, display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', height: '100%', minHeight: '250px' }}> {/* Zwiększono minHeight */}
            <Typography variant="h1" sx={{ fontSize: '6rem' }}>{getWeatherIcon()}</Typography>
            <Typography variant="h2" sx={{ my: 1 }}>{temperature_c?.toFixed(1)}°C</Typography>
            <Typography color="text.secondary">Stacja: {data.device_id}</Typography>
        </Paper>
    )
}

const StormAlert = ({ level }) => {
  const alerts = [
    { severity: 'success', title: 'Pogoda stabilna' },
    { severity: 'info', title: 'Możliwe pogorszenie pogody - ciśnienie lekko spada' },
    { severity: 'warning', title: 'Ostrzeżenie o burzy - ciśnienie gwałtownie spada!' },
  ];
  const currentAlert = alerts[level ?? 0];
  if (level === null || level === undefined) return null;

  return (
    <Alert severity={currentAlert.severity} sx={{ width: '100%' }}>
      <AlertTitle>{currentAlert.title}</AlertTitle>
    </Alert>
  );
};

// --- Główny Dashboard ---

const Dashboard = () => { 
  const [latestData, setLatestData] = useState(null);
  const [historicalData, setHistoricalData] = useState([]);
  const [timeRange, setTimeRange] = useState('1h');
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isTerminalVisible, setIsTerminalVisible] = useState(false); 

  useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      try {
        let fromDate;
        switch(timeRange) {
            case '24h': fromDate = sub(new Date(), { hours: 24 }); break;
            case '7d': fromDate = sub(new Date(), { days: 7 }); break;
            default: fromDate = sub(new Date(), { hours: 1 }); break;
        }

        const { data, error: supabaseError } = await supabase
          .from('weather_data')
          .select('*')
          .gte('created_at', fromDate.toISOString())
          .order('created_at', { ascending: true });

        if (supabaseError) throw supabaseError;

        if (data && data.length > 0) {
          setLatestData(data[data.length - 1]);
          setHistoricalData(data);
        }
      } catch (err) {
        console.error("Błąd pobierania danych: ", err);
        setError('Nie udało się załadować danych. Sprawdź konsolę.');
      } finally {
        setIsLoading(false);
      }
    };

    fetchData();

    const subscription = supabase.channel('weather_data_changes')
      .on('postgres_changes', { event: 'INSERT', schema: 'public', table: 'weather_data' }, (payload) => {
        setLatestData(payload.new);
        setHistoricalData(prevData => [...prevData.slice(-999), payload.new]);
      })
      .subscribe();

    return () => {
      supabase.removeChannel(subscription);
    };
  }, [timeRange]);

  // --- Interpretacja Danych ---
  const lightStatus = useMemo(() => {
      if (!latestData) return { text: '--', value: 0 };
      const avgLight = (latestData.light_1_raw + latestData.light_2_raw) / 2;
      if (avgLight < 500) return { text: 'Bardzo ciemno', value: avgLight };
      if (avgLight < 1500) return { text: 'Półmrok', value: avgLight };
      if (avgLight < 3000) return { text: 'Jasno', value: avgLight };
      return { text: 'Bardzo jasno', value: avgLight };
  }, [latestData]);

  const rainStatus = useMemo(() => {
      if (!latestData) return '--';
      if (latestData.is_raining) {
          if (latestData.rain_raw < 1000) return 'Ulewa';
          if (latestData.rain_raw < 2000) return 'Pada';
          return 'Lekko kropi';
      }
      return 'Sucho';
  }, [latestData]);

  // --- Konfiguracja Wykresu ---
  const chartData = useMemo(() => ({
    labels: historicalData.map(d => format(new Date(d.created_at), 'HH:mm')),
    datasets: [
      {
        label: 'Temperatura (°C)',
        data: historicalData.map(d => d.temperature_c),
        borderColor: '#ff6384',
        backgroundColor: 'rgba(255, 99, 132, 0.2)',
        yAxisID: 'y',
        fill: true,
      },
      {
        label: 'Ciśnienie (hPa)',
        data: historicalData.map(d => d.pressure_hpa),
        borderColor: '#36a2eb',
        backgroundColor: 'rgba(54, 162, 235, 0.2)',
        yAxisID: 'y1',
        hidden: true, // Ukryty domyślnie dla czytelności
      },
    ],
  }), [historicalData]);

  const chartOptions = useMemo(() => ({
    responsive: true,
    maintainAspectRatio: false,
    interaction: {
        mode: 'index',
        intersect: false,
    },
    plugins: {
      legend: { position: 'top' },
      title: { display: true, text: 'Historia pomiarów (ostatnie 100 odczytów)' },
    },
    scales: {
        y: {
            type: 'linear',
            display: true,
            position: 'left',
            title: { display: true, text: '°C / %' }
        },
        y1: {
            type: 'linear',
            display: true,
            position: 'right',
            title: { display: true, text: 'hPa' },
            grid: { drawOnChartArea: false }, // Nie rysuj siatki dla drugiej osi
        },
    }
  }), []); 

  if (isLoading && !latestData) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
        <CircularProgress />
        <Typography variant="h6" sx={{ ml: 2 }}>Ładowanie danych...</Typography>
      </Box>
    );
  }

  if (error) {
    return <Container><Alert severity="error"><h4>Błąd Aplikacji</h4><p>{error}</p></Alert></Container>;
  }

  return (
    <Box>
      <AppBar position="static" color="transparent" elevation={0} sx={{ borderBottom: '1px solid', borderColor: 'divider' }}>
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>Weather Dashboard</Typography>
          <Button variant="outlined" color="inherit" size="small" onClick={() => setIsTerminalVisible(true)} sx={{ mr: 2 }}>Serial Monitor</Button>
        </Toolbar>
      </AppBar>

      {isTerminalVisible && <SerialMonitor onClose={() => setIsTerminalVisible(false)} />} 

      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Grid container spacing={3}>
          <Grid item xs={12}><StormAlert level={latestData?.storm_warning_level} /></Grid>

          <Grid item xs={12} md={4}><MainWeatherDisplay data={latestData} /></Grid>
          <Grid item xs={12} md={8}>
            <Grid container spacing={3}>
              <Grid item xs={12} sm={6} md={6}><StatCard title="Wilgotność" value={latestData?.humidity_pct?.toFixed(1)} unit="%" icon="💧" tooltip="Względna wilgotność powietrza" /></Grid>
              <Grid item xs={12} sm={6} md={6}><StatCard title="Ciśnienie" value={latestData?.pressure_hpa?.toFixed(1)} unit=" hPa" icon="💨" trend={latestData?.pressure_trend_hpa_hr} tooltip="Ciśnienie atmosferyczne znormalizowane do poziomu morza" /></Grid>
              <Grid item xs={12} sm={6} md={6}><StatCard title="Temp. odczuwalna" value={latestData?.heat_index_c?.toFixed(1)} unit="°C" icon="🥵" tooltip="Indeks cieplny, kombinacja temperatury i wilgotności" /></Grid>
              <Grid item xs={12} sm={6} md={6}><StatCard title="Punkt rosy" value={latestData?.dew_point_c?.toFixed(1)} unit="°C" icon="🧊" tooltip="Temperatura, przy której para wodna zaczyna się skraplać" /></Grid>
            </Grid>
          </Grid>

          <Grid item xs={12}>
            <Card>
              <CardContent>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    <Typography variant="h6">Historia pomiarów</Typography>
                    <ButtonGroup size="small">
                        <Button variant={timeRange === '1h' ? 'contained' : 'outlined'} onClick={() => setTimeRange('1h')}>1H</Button>
                        <Button variant={timeRange === '24h' ? 'contained' : 'outlined'} onClick={() => setTimeRange('24h')}>24H</Button>
                        <Button variant={timeRange === '7d' ? 'contained' : 'outlined'} onClick={() => setTimeRange('7d')}>7D</Button>
                    </ButtonGroup>
                </Box>
                <Box sx={{ height: 300, mt: 2}}>
                    {historicalData.length > 1 ? <Line options={chartOptions} data={chartData} /> : <Typography>Za mało danych do narysowania wykresu...</Typography>}
                </Box>
              </CardContent>
            </Card>
          </Grid>

          <Grid item xs={12} sm={6} md={3}><StatCard title="Wysokość" value={latestData?.altitude_m?.toFixed(0)} unit=" m n.p.m." icon="🏔️" tooltip="Przybliżona wysokość na podstawie ciśnienia" /></Grid>
          <Grid item xs={12} sm={6} md={3}><StatCard title="Zachmurzenie" value={latestData?.cloud_cover_pct?.toFixed(0)} unit="%" icon="☁️" tooltip="Procentowe oszacowanie pokrywy chmur na podstawie światła" /></Grid>
          <Grid item xs={12} sm={6} md={3}><StatCard title="Poziom światła" value={lightStatus.text} unit={`(${lightStatus.value})`} icon="💡" tooltip="Interpretacja danych z fotorezystorów" /></Grid>
          <Grid item xs={12} sm={6} md={3}><StatCard title="Opady" value={rainStatus} unit="" icon="🌧️" tooltip={`Surowa wartość: ${latestData?.rain_raw}`} /></Grid>

        </Grid>
      </Container>
    </Box>
  );
};

export default Dashboard;