
import React, { useState, useEffect, useRef } from 'react';
import { supabase } from './supabaseClient';
import { Box, Typography, Paper, IconButton, CircularProgress } from '@mui/material';


// Prosta lokalna ikona zamknięcia, żeby uniknąć zależności
const LocalCloseIcon = (props) => (
    <svg {...props} xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24" fill="currentColor">
        <path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"/>
    </svg>
);

const SerialMonitor = ({ onClose }) => {
    const [logs, setLogs] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const scrollRef = useRef(null);

    useEffect(() => {
        // Funkcja do przewijania na dół
        const scrollToBottom = () => {
            if (scrollRef.current) {
                scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
            }
        };

        // Pobierz ostatnie 50 logów przy starcie
        const fetchInitialLogs = async () => {
            setIsLoading(true);
            const { data, error } = await supabase
                .from('device_logs')
                .select('*')
                .order('created_at', { ascending: false })
                .limit(50);

            if (error) {
                console.error("Błąd pobierania logów:", error);
                setLogs([{ id: 'error', created_at: new Date().toISOString(), message: 'Błąd ładowania logów.' }]);
            } else {
                setLogs(data.reverse());
            }
            setIsLoading(false);
        };

        fetchInitialLogs();

        // Subskrybuj nowe logi
        const subscription = supabase.channel('device_logs_realtime')
            .on('postgres_changes', { event: 'INSERT', schema: 'public', table: 'device_logs' }, (payload) => {
                setLogs(currentLogs => [...currentLogs, payload.new]);
            })
            .subscribe();
        
        return () => {
            supabase.removeChannel(subscription);
        };
    }, []);

    // Efekt do auto-scrollowania
    useEffect(() => {
        const scrollToBottom = () => {
            if (scrollRef.current) {
                scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
            }
        };
        scrollToBottom();
    }, [logs]);

    return (
        <Paper elevation={12} sx={{
            position: 'fixed',
            bottom: '20px',
            right: '20px',
            width: 'clamp(300px, 60vw, 800px)',
            height: '400px',
            zIndex: 1500,
            display: 'flex',
            flexDirection: 'column',
            backgroundColor: 'rgba(0, 0, 0, 0.85)',
            backdropFilter: 'blur(5px)',
            border: '1px solid rgba(255, 255, 255, 0.2)'
        }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', p: 1, borderBottom: '1px solid rgba(255, 255, 255, 0.2)' }}>
                <Typography variant="body2" sx={{ fontFamily: 'monospace', color: '#00ff00' }}>[SERIAL MONITOR]</Typography>
                <IconButton onClick={onClose} size="small">
                    <LocalCloseIcon sx={{ color: 'white' }} />
                </IconButton>
            </Box>
            <Box ref={scrollRef} sx={{ p: 2, overflowY: 'auto', flexGrow: 1 }}>
                {isLoading ? (
                    <Box sx={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100%'}}><CircularProgress size={30} /></Box>
                ) : (
                    logs.map(log => (
                        <Box key={log.id} sx={{ display: 'flex' }}>
                            <Typography variant="body2" sx={{ fontFamily: 'monospace', color: '#888', mr: 2 }}>
                                {new Date(log.created_at).toLocaleTimeString()}
                            </Typography>
                            <Typography variant="body2" sx={{ fontFamily: 'monospace', color: '#00ff00', whiteSpace: 'pre-wrap' }}>
                                {log.message}
                            </Typography>
                        </Box>
                    ))
                )}
            </Box>
        </Paper>
    );
};

export default SerialMonitor;
